export default Hankyeolk;

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        author {
          name
          enName
        }
      }
    }

    allMarkdownRemark(
      filter: { fields: { postSlug: { ne: null } } }
      sort: { frontmatter: { createdAt: DESC } }
    ) {
      edges {
        node {
          fields {
            postSlug
          }
          id
          excerpt(pruneLength: 300, truncate: true)
          frontmatter {
            createdAt
            title
            categories
          }
        }
      }
    }
  }
`;
